/** ì—¬ëŸ¬ í™”ë©´ì—ì„œ ì‚¬ìš©ë˜ëŠ” í•¸ë“¤ëŸ¬ê°€ ìˆë‹¤ë©´ í•˜ë‚˜ì˜ ê°ì²´ì— ë‹´ì•„ë‘ê³  í•„ìš”í•  ë•Œ ì“°ë„ë¡ í•˜ì˜€ìŠµë‹ˆë‹¤. */
/** singleton */
export const { setListener, emitListener } = (function () {
  const eventListeners = {};

  return {
    setListener: (eventName, eventListener) => {
      eventListeners[eventName] = eventListener;
    },
    emitListener: (eventName, e) => {
      const eventListener = eventListeners[eventName];
      eventListener(e);
    },
    removeListener: (eventName) => {
      delete eventListeners[eventName];
    },
  };
})();
